


























































































































































































@import '@/styles/element-ui-new.scss';
@import '@/styles/public.scss';

.content-box {
  .plan-content-box {
    padding: 0 40px;
    .list-item {
      display: flex;
      justify-content: space-between;
      height: 40px;
      line-height: 40px;

      .list-items {
        display: flex;
        flex: 1;

        .item-title,
        .item-content span {
          font-size: 12px;
        }

        .item-title {
          width: 100px;
          text-align: right;
          margin-right: 5px;
        }

        .item-content {
          span {
            color: #999999;
          }

          .view-button {
            margin-left: 10px;
          }
          /deep/ .el-button {
            color: #4b77be;
            border-color: #4b77be;
          }
          /deep/ .el-button:hover {
            background: #4b77be;
            color: #ffffff;
          }
        }
      }
      .has-two-item {
        width: 46%;
      }
    }

    .time-box {
      padding: 0 30px;
      .list-item {
        display: flex;
        justify-content: space-between;
        height: 40px;
        line-height: 40px;

        .list-items {
          width: 43%;
          font-size: 12px;
          color: #999999;
          &:nth-child(odd) div{
            width: 100%;
            text-align: right;
            padding-right: 30px;
          }
          &:nth-child(even) div{
            width: 100%;
            padding-left: 30px;
          }
        }
      }
    }
  }
}
