



























































































































































































































































































































































































@import '@/styles/element-ui-new.scss';
@import '@/styles/public.scss';

.task_management_pages {
  padding: 10px;

  .header-box {
    display: flex;
    justify-content: space-between;

    .header-left {
      display: flex;

      .search-box {
        margin-left: 20px;
        display: flex;

        .search-button {
          margin-left: 5px;
        }
      }
    }

    .header-right {
      display: flex;
      .date-box {
        position: relative;
        margin-right: 5px;
        .attendance-box {
          position: absolute;
          top: 0;
          right: 0;
          width: 74px;
          height: 28px;
          opacity: 0;
          cursor: pointer;
          z-index: 30;
        }
      }
    }
  }

  .content-box {
    margin-top: 10px;

    .table-box {
      box-sizing: border-box;
    }

    .page_box {
      margin-top: 10px;
    }
  }
}
